exports.push([module.id, "\n.Box[data-v-63f3b52f]{\n    overflow-x: hidden;\n}\n.font[data-v-63f3b52f]{\n    padding: 0 20px;\n    box-sizing: border-box;\n}\n.header[data-v-63f3b52f]{\n    width: 100%;\n    height: 60px;\n    line-height: 60px;\n    background: #549caf;\n}\n.font .LogoName[data-v-63f3b52f]{\n    font-weight: bolder;\n    font-size: 20px;\n    font-family: \"Microsoft YaHei\";\n    display: inline-block;\n    margin-right: 20px;\n}\n.font .label .el-tag[data-v-63f3b52f]{\n    margin: 0 4px;\n}\n.font .title[data-v-63f3b52f]{\n    margin-top: 10px;\n}\n.message[data-v-63f3b52f]{\n    padding-top: 25px;\n    box-sizing: border-box;\n    height: 60px;\n    border-bottom: 1px solid #a7a7a7;\n    background: -webkit-linear-gradient(top,rgba(243,214,177,0),rgba(243,214,177,0.4));\n    margin-top: 6px;\n}\n.message span[data-v-63f3b52f]{\n    display: inline-block;\n    padding: 5px 30px;\n    box-sizing: border-box;\n    font-weight: 600;\n    font-size: 14px;\n    font-family: \"Microsoft YaHei\";\n}\n.el-breadcrumb[data-v-63f3b52f]{\n    line-height: 35px;\n}\n.Data[data-v-63f3b52f]{\n    padding: 24px 12px;\n    box-sizing: border-box;\n}\n.Sty_title[data-v-63f3b52f]{\n    display: inline-block;\n    padding: 0px 8px;\n    box-sizing: border-box;\n    font-size: 14px;\n    color: #666;\n    border-left: 4px solid #365d98;\n    height: 16px;\n    line-height: 16px;\n    margin: 10px 0;\n}\ntable.gridtable[data-v-63f3b52f] {\n    width: 100%;\n    font-family: \"Microsoft YaHei\";\n    font-size:12px;\n    color:#333333;\n    font-weight: bold;\n    border-width: 1px;\n    border-color: #d7dbe0;\n    border-collapse: collapse;\n}\ntable.gridtable th[data-v-63f3b52f] {\n    text-align: left;\n    border-width: 1px;\n    padding: 5px;\n    background-color: #eef1f6;\n    padding-left: 15px;\n    box-sizing: border-box;\n}\ntable.odd tr[data-v-63f3b52f]:nth-child(2n){\n    background-color: #fafafa !important;\n}\ntable.gridtable tr[data-v-63f3b52f]{\n    background-color: white;\n}\ntable.gridtable td[data-v-63f3b52f] {\n    padding: 5px;\n    border-color: #d7dbe0;\n    text-align: left;\n    padding-left: 15px;\n    box-sizing: border-box;\n}\ntable.gridtable tbody[data-v-63f3b52f],table.gridtable thead[data-v-63f3b52f]{\n    border-width: 1px;\n    border-style: solid;\n}\n.add_two[data-v-63f3b52f]{\n    display: inline-block;\n    margin:10px 0;\n    font-family: \"Microsoft YaHei\";\n}\n.add_two span[data-v-63f3b52f]{\n    font-size: 16px;\n}\n.add_two font[data-v-63f3b52f]{\n    font-size: 12px;\n    color: #666;\n}\n.user[data-v-63f3b52f],.email[data-v-63f3b52f]{\n    font-size: 14px;\n    color: black;\n    font-weight: bold;\n}\n.sty_sm_title[data-v-63f3b52f]{\n    font-family: \"Microsoft YaHei\";\n    font-size: 14px;\n    display: block;\n    margin: 10px 0 5px 5px;\n    font-weight: bold;\n}\n.only_editor[data-v-63f3b52f]{\n    padding: 10px;\n    box-sizing: border-box;\n    background: white;\n}\n.btn[data-v-63f3b52f]{\n    padding-left: 45%;\n    margin-top: 15px;\n    text-align: center;\n    box-sizing: border-box;\n}\n.mainContent[data-v-63f3b52f]{\n    font-family: \"Microsoft YaHei\";\n    font-size: 14px;\n    display: inline-block;\n    padding: 5px 6px;\n    box-sizing: border-box;\n    color: #333333;\n    line-height: 22px;\n    width: 100%;\n}\n", "", {"version":3,"sources":["C:/Users/hp/Desktop/4/YaoQ.Beehive.CreditGradeWebSite/src/app3/pages/src/app3/pages/EditCredit.vue"],"names":[],"mappings":";AAgTA;IACA,mBAAA;CACA;AACA;IACA,gBAAA;IACA,uBAAA;CACA;AACA;IACA,YAAA;IACA,aAAA;IACA,kBAAA;IACA,oBAAA;CACA;AACA;IACA,oBAAA;IACA,gBAAA;IACA,+BAAA;IACA,sBAAA;IACA,mBAAA;CACA;AAEA;IACA,cAAA;CACA;AACA;IACA,iBAAA;CACA;AACA;IACA,kBAAA;IACA,uBAAA;IACA,aAAA;IACA,iCAAA;IACA,mFAAA;IACA,gBAAA;CACA;AACA;IACA,sBAAA;IACA,kBAAA;IACA,uBAAA;IACA,iBAAA;IACA,gBAAA;IACA,+BAAA;CACA;AACA;IACA,kBAAA;CACA;AACA;IACA,mBAAA;IACA,uBAAA;CACA;AACA;IACA,sBAAA;IACA,iBAAA;IACA,uBAAA;IACA,gBAAA;IACA,YAAA;IACA,+BAAA;IACA,aAAA;IACA,kBAAA;IACA,eAAA;CACA;AACA;IACA,YAAA;IACA,+BAAA;IACA,eAAA;IACA,cAAA;IACA,kBAAA;IACA,kBAAA;IACA,sBAAA;IACA,0BAAA;CACA;AACA;IACA,iBAAA;IACA,kBAAA;IACA,aAAA;IACA,0BAAA;IACA,mBAAA;IACA,uBAAA;CACA;AAEA;IACA,qCAAA;CACA;AACA;IACA,wBAAA;CACA;AACA;IACA,aAAA;IACA,sBAAA;IACA,iBAAA;IACA,mBAAA;IACA,uBAAA;CACA;AACA;IACA,kBAAA;IACA,oBAAA;CACA;AACA;IACA,sBAAA;IACA,cAAA;IACA,+BAAA;CACA;AACA;IACA,gBAAA;CACA;AACA;IACA,gBAAA;IACA,YAAA;CACA;AACA;IACA,gBAAA;IACA,aAAA;IACA,kBAAA;CACA;AACA;IACA,+BAAA;IACA,gBAAA;IACA,eAAA;IACA,uBAAA;IACA,kBAAA;CACA;AACA;IACA,cAAA;IACA,uBAAA;IACA,kBAAA;CACA;AACA;IACA,kBAAA;IACA,iBAAA;IACA,mBAAA;IACA,uBAAA;CACA;AACA;IACA,+BAAA;IACA,gBAAA;IACA,sBAAA;IACA,iBAAA;IACA,uBAAA;IACA,eAAA;IACA,kBAAA;IACA,YAAA;CACA","file":"EditCredit.vue","sourcesContent":["<template>\r\n    <div id=\"pdfDom\">\r\n        <div class=\"Box\" id=\"Box\">\r\n            <Vauthor :show=\"show\"></Vauthor>\r\n            <div class=\"header\"></div>\r\n            <Vheader :reportData=\"reportData\" v-if=\"reportData\" :show=\"show\"></Vheader>\r\n            <div class=\"Data\">\r\n                <el-row :gutter=\"20\">\r\n                    <!--左边表格区域-->\r\n                    <el-col :span=\"6\">\r\n                        <LeftTable :LeftTableData=\"LeftTableData\" v-if=\"LeftTableData\"></LeftTable>\r\n                    </el-col>\r\n\r\n                    <!--右边富文本编辑区域-->\r\n                    <el-col :span=\"18\">\r\n                        <div class=\"AllUeditor\">\r\n                            <span class=\"Sty_title\">概况</span>\r\n                            <!--公司概况-->\r\n                            <label>\r\n                                <span class=\"sty_sm_title\">Ⅰ.公司概况</span>\r\n                                <label class=\"mainContent\" v-html=\"Company_profile\"></label>\r\n                            </label>\r\n\r\n\r\n                            <!--利润信息-->\r\n                            <label>\r\n                                <span class=\"sty_sm_title\">Ⅱ.经营情况</span>\r\n                                <label class=\"mainContent\" v-html=\"Profit_information\"></label>\r\n                            </label>\r\n\r\n                            <!--现金流情况-->\r\n                            <label>\r\n                                <span class=\"sty_sm_title\">Ⅲ.现金流情况</span>\r\n                                <label class=\"mainContent\" v-html=\"Cash\"></label>\r\n                            </label>\r\n\r\n                            <!--资产负债情况-->\r\n                            <label>\r\n                                <span class=\"sty_sm_title\">Ⅳ.资产负债情况</span>\r\n                                <label class=\"mainContent\" v-html=\"Assets\"></label>\r\n                            </label>\r\n                            <!--风险-->\r\n                            <label>\r\n                                <span class=\"sty_sm_title\">Ⅴ.风险</span>\r\n                                <label class=\"mainContent\" v-html=\"Construction\"></label>\r\n                            </label>\r\n\r\n                            <!--结论-->\r\n                            <label>\r\n                                <span class=\"sty_sm_title\">VI.结论</span>\r\n                                <label class=\"mainContent\" v-html=\"Risk\"></label>\r\n                            </label>\r\n\r\n                        </div>\r\n                        <el-row class=\"btn\" id=\"btn\">\r\n                            <el-button type=\"primary\" size=\"medium \" style=\"background: #1dacca;border-color:  #1dacca\" @click=\"EditPage()\">编辑</el-button>\r\n                            <el-button type=\"primary\" size=\"medium \" style=\"background: #1dacca;border-color:  #1dacca\" @click=\"pdf\">下载PDF</el-button>\r\n                            <el-button type=\"info\"  size=\"medium \" @click=\"goBack()\">关闭</el-button>\r\n                        </el-row>\r\n                    </el-col>\r\n                </el-row>\r\n            </div>\r\n        </div>\r\n\r\n        <loadding v-if=\"showLoadding\" :loaddingMsg=\"loaddingMsg\"></loadding>\r\n    </div>\r\n\r\n</template>\r\n\r\n<script>\r\n  import {IpUrl} from '../config/IpUrl'\r\n  import html2Canvas from 'html2canvas'\r\n  import JsPDF from 'jspdf'\r\n  import  LeftTable from '../components/page/LeftTable.vue'\r\n  import  Vauthor from '../components/page/header-bar2.vue'\r\n  import Vheader from '../components/page/Vheader.vue'\r\n  import Loadding from '../components/page/loadding.vue'\r\n  import {setStore,getStore,getCookie} from '../config/mUtils'\r\n//  import {autoTableHtmlToJson,autoTable} from '../static/js/autotable'\r\n  export default {\r\n    components:{\r\n      LeftTable,Vheader,Vauthor,Loadding\r\n    },\r\n    data() {\r\n      return {\r\n        \"htmlTitle\":\"\",\r\n        \"Company_profile\":\"\",\r\n        \"Profit_information\":\"\",\r\n        \"Cash\":\"\",\r\n        \"Assets\":\"\",\r\n        \"Risk\":\"\",\r\n        \"Construction\":\"\",\r\n        \"reportData\":{\r\n          \"creditRatingReportDetail\":{}\r\n        },\r\n        \"LeftTableData\":{\r\n          \"createUser\":\"\",\r\n          \"email\":\"\",\r\n          \"ratingAgency\":\"\",//评级机构\r\n          \"corporateCreditRating\":\"\",//主体评级\r\n          \"ratingOutlook\":\"\",//评级展望\r\n          \"debtCreditRating\":\"\",//债项评级\r\n          \"upTo\":\"\",//截止至\r\n          \"nonCurLiabDueWithin1y\":\"\",//一年内到期\r\n          \"stBondsPayable\":\"\",//一年以内\r\n          \"bondsPayable\":\"\",//一年以上\r\n          \"stBorrow\":\"\",//短期\r\n          \"ltBorrow\":\"\",//长期\r\n          \"operRev\":\"\",//营业收入\r\n          \"ebitda\":\"\",//EBITDA\r\n          \"ebitdaRate\":\"\",//EBITDA%\r\n          \"cfo\":\"\",//CFO\r\n          \"ffo\":\"\",//FFO\r\n          \"assetLiabilityRatio\":\"\",//资产负债率(%)\r\n          \"grossDebt_Ebitda\":\"\",//总债务/EBITDA\r\n          \"currentRatio\":\"\",//流动比率(x)\r\n          \"quickRatio\":\"\",//速动比率(x)\r\n          \"grossProfitRate\":\"\",//毛利率\r\n\r\n          \"operRev1\":\"\",//营业收入\r\n          \"ebitda1\":\"\",//EBITDA\r\n          \"ebitdaRate1\":\"\",//EBITDA%\r\n          \"cfo1\":\"\",//CFO\r\n          \"ffo1\":\"\",//FFO\r\n          \"assetLiabilityRatio1\":\"\",//资产负债率(%)\r\n          \"grossDebt_Ebitda1\":\"\",//总债务/EBITDA\r\n          \"currentRatio1\":\"\",//流动比率(x)\r\n          \"quickRatio1\":\"\",//速动比率(x)\r\n          \"grossProfitRate1\":\"\",//毛利率\r\n\r\n          \"Array\":[],\r\n        },\r\n        \"showLoadding\":true,\r\n        \"loaddingMsg\":'正在获取报告详情，请稍等',\r\n        \"show\":true\r\n      }\r\n    },\r\n    computed: {\r\n\r\n    },\r\n    updated() {\r\n      let imgData=document.querySelectorAll(\".mainContent  img\");\r\n      if(imgData.length>0){\r\n        for(let item of imgData){\r\n          item.style.width=\"100%\";\r\n        }\r\n      }\r\n    },\r\n    methods: {\r\n//      跳转至编辑页面\r\n      EditPage(){\r\n        let Url=window.location.href;\r\n        console.log(Url,\"******Url*******\");\r\n        let CreditId=Url.split(\"?\")[1].split(\"=\")[1];\r\n        console.log(CreditId,\"*******CreditId***********\");\r\n        this.CreditId=CreditId;\r\n        this.$router.push('NewCredit?id='+CreditId);\r\n      },\r\n      pdf(){\r\n        document.querySelector(\"#btn\").style.display=\"none\";\r\n        this.show=false;\r\n        setTimeout(()=>{\r\n          var title = this.htmlTitle;\r\n          html2Canvas(document.querySelector('#pdfDom'), {\r\n             useCORS: true,\r\n          }).then( (canvas)=> {\r\n            let contentWidth = canvas.width;\r\n            let contentHeight = canvas.height;\r\n            let pageHeight = contentWidth / 592.28 * 841.89;\r\n            let leftHeight = contentHeight;\r\n            let position = 0;\r\n            let imgWidth = 595.28;\r\n            let imgHeight = 592.28 / contentWidth * contentHeight;\r\n            let pageData = canvas.toDataURL('image/png', 1.0);\r\n            let PDF = new JsPDF('', 'pt', 'a4');\r\n            PDF.setFontSize(30);\r\n            // //这里就是把将table写入到pdf里面。\r\n//           var res = PDF.autoTableHtmlToJson(document.getElementsByClassName(\"table\")[0], true);\r\n//           PDF.autoTable(res.columns, res.data);\r\n            if (leftHeight < pageHeight) {\r\n              PDF.addImage(pageData, 'PNG', 0, 0, imgWidth, imgHeight)\r\n            } else {\r\n              while (leftHeight > 0) {\r\n                PDF.addImage(pageData, 'PNG', 0, position, imgWidth, imgHeight);\r\n                leftHeight -= pageHeight;\r\n                position -= 841.89;\r\n                if (leftHeight > 0) {\r\n                  PDF.addPage();\r\n                }\r\n              }\r\n            }\r\n            PDF.save(title + '.pdf');\r\n            document.querySelector(\"#btn\").style.display=\"block\";\r\n            this.show=true;\r\n          })\r\n        },200);\r\n      },\r\n      //根据id获取信评报告明细\r\n      getReportDetails(){\r\n        let Url=window.location.href;\r\n        console.log(Url,\"******Url*******\");\r\n        let CreditId=Url.split(\"?\")[1].split(\"=\")[1];\r\n        console.log(CreditId,\"*******CreditId***********\");\r\n        axios({\r\n          method: 'get',\r\n          url: '/v1/creditRating/reportDetail',\r\n          headers: {\r\n            'Authorization':\"bearer \" +getCookie(\"Token\"),\r\n          },\r\n          params:{\r\n            \"id\":CreditId,\r\n          },\r\n        }).then( (response)=> {\r\n          if(response.data.status == 401){\r\n            this.$message({\r\n              message: \"Token已经过期\"\r\n            });\r\n            this.$router.push(\"/login\");\r\n          }\r\n          console.log(response.data,\"通过id拉取信评报告明细\");\r\n          if(response.data.status==200&&response.data.data!=null){\r\n            this.showLoadding=false;\r\n            this.reportData=response.data.data;\r\n//            this.LeftTableData=response.data.data;\r\n\r\n            this.LeftTableData.createUser=response.data.data.createUser;\r\n            this.LeftTableData.email=response.data.data.email;\r\n            this.LeftTableData.ratingAgency=response.data.data.creditRatingReportDetail.ratingAgency;\r\n            this.LeftTableData.corporateCreditRating=response.data.data.creditRatingReportDetail.corporateCreditRating;\r\n            this.LeftTableData.ratingOutlook=response.data.data.creditRatingReportDetail.ratingOutlook;\r\n            this.LeftTableData.debtCreditRating=response.data.data.creditRatingReportDetail.debtCreditRating;\r\n            this.LeftTableData.upTo=JSON.parse(response.data.data.creditRatingReportDetail.debt).upTo;\r\n            this.LeftTableData.nonCurLiabDueWithin1y=JSON.parse(response.data.data.creditRatingReportDetail.debt).nonCurLiabDueWithin1y;\r\n            this.LeftTableData.stBondsPayable=JSON.parse(response.data.data.creditRatingReportDetail.debt).stBondsPayable;\r\n            this.LeftTableData.bondsPayable=JSON.parse(response.data.data.creditRatingReportDetail.debt).bondsPayable;\r\n            this.LeftTableData.stBorrow=JSON.parse(response.data.data.creditRatingReportDetail.debt).stBorrow;\r\n            this.LeftTableData.ltBorrow=JSON.parse(response.data.data.creditRatingReportDetail.debt).ltBorrow;\r\n\r\n\r\n            let financialOverview=JSON.parse(response.data.data.creditRatingReportDetail.financialOverview);\r\n            console.log(financialOverview,\"financialOverviewfinancialOverviewfinancialOverviewfinancialOverview\");\r\n            let Array=[];\r\n            for(let key in financialOverview){\r\n              Array.push(key);\r\n            }\r\n            this.LeftTableData.Array=Array;\r\n            if(Array.length>0){\r\n              this.LeftTableData.operRev=financialOverview[Array[0]].operRev;\r\n              this.LeftTableData.quickRatio=financialOverview[Array[0]].quickRatio;\r\n              this.LeftTableData.assetLiabilityR